@import url(https://fonts.googleapis.com/css?family=Montserrat:400,400i,600&display=swap);
@import url(https://use.typekit.net/ixk5mhv.css);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
#bgvid {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
}

.caption {
  position: absolute;
  top: 30%;
  width: 100%;
  color: white;
}

.caption h1 {
  margin: 0;
  height: 45px;
  font-family: "program", sans-serif;
  font-size: 3.5rem;
  font-weight: 700;
  letter-spacing: 0.15rem;
  text-shadow: 0.1rem 0.1rem 0.8rem black;
}

.caption h2 {
  height: 50px;
  margin: 0;
  font-family: "program", sans-serif;
  font-size: 2rem;
  /* letter-spacing: 0.15rem; */
  text-shadow: 0.1rem 0.1rem 0.8rem black;
}
.caption h3 {
  font-size: 2rem;
  text-shadow: 0.1rem 0.1rem 0.5rem black;
  padding-bottom: 10rem;
}

@media (max-width: 768px) {
  .caption h1 {
    font-size: 1.4rem;
    letter-spacing: 0.15rem;
    padding-bottom: 0.5rem;
  }

  .caption h3 {
    font-size: 1.1rem;
    padding-bottom: 0.5rem;
  }
}

.credit {
  position: absolute;
  bottom: 0rem;
  right: 2rem;
}

.credit a {
  text-decoration: none;
  color: whitesmoke;
  text-shadow: 0.1rem 0.1rem 0.5rem black;
}

.credit img {
  border-radius: 50%;
  opacity: 0.5;
}

.credit img:hover {
  opacity: 1;
}

.btn-lg {
  border-width: medium;
  font-size: 1.1rem;
  padding: 0.6rem 1.3rem;
  position: absolute;
  margin-left: -50px;
  top: 50%;
  margin-top: 8rem;
}

@media (max-width: 768px) {
  .btn-lg {
    font-size: 1rem;
    padding: 0.5rem 1rem;
  }
}

body {
  background: radial-gradient(#e5e5e5, #ffff, #e5e5e5);
  font-family: "Montserrat", sans-serif;
}

.card {
  width: 20rem;
  height: 20rem;
  margin-bottom: 2rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  position: relative;
}

.card:hover {
  box-shadow: 5px 10px 20px 1 px rgba(0, 0, 0, 0.253) !important;
}

.card-body {
  padding: 0 !important;
}

.card-text {
  font-size: 0.9rem;
  padding: 0.4rem 1.9rem;
}

.overflow {
  overflow: hidden;
}

.card-img-top {
  transform: scale(1);
  transition: transform 0.5s ease;
  width: 250px;
  height: 200px;
  margin-top: 1.5rem;
}

.card-img-top:hover {
  transform: scale(1.8);
}

.card-text {
  padding: 0rem 0.7rem;
  margin-bottom: 0.7rem;
}

.card-title {
  margin: 0.25rem;
  margin-top: 0.6rem;
}

.card-body {
  height: 400px;
}

:root {
  --sky: linear-gradient(120deg, #17a2b8 0%, #c2e9fb 100%);
}

.tags {
  background: linear-gradient(120deg, #17a2b8 0%, #c2e9fb 100%);
  background: var(--sky);
  padding: 0.4rem 1rem;
  border-radius: 3rem;
  margin: 0.4rem 0.6rem;
  cursor: pointer;
  color: #0f525c;
}

.navbar {
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: space-around;
}

.navbar-brand {
  height: 2rem;
  font-family: "Lato", sans-serif;
  padding: 0.5rem, 0.5rem;
}

.navbar-dark.navbar-brand a:hover {
  color: rgb(15, 82, 92);
}

.footer {
  height: 100%;
  background: #0f0f0f;
  position: relative;
}

.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.container > div {
  flex-grow: 1;
  flex-basis: 0;
  padding: 3rem 0.9rem;
}

.container h2 {
  color: #ffffff;
}

.form-element {
  background: black;
  display: inline-block;
}

.form-element input {
  padding: 0.5rem 0.7rem;
  background: transparent;
  color: white;
  /* font-family: var(--Josefin); */
  font-size: 1rem;
  width: 74%;
}

.form-element span {
  background: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  padding: 0.5rem 0.7rem;
  cursor: pointer;
}

.instagram div > img {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 0.3rem 0.4rem;
}

.follow div i {
  color: #ffffff;
  padding: 0 0.4rem;
}

.rights {
  justify-content: center;
  /* font-family: var(--Josefin); */
}

.rights h4 a {
  color: #ffffff;
}

.move-up {
  position: absolute;
  right: 6%;
  top: 50%;
}

.move-up a {
  text-decoration: none;
  color: #104f55;
}

.move-up span:hover {
  color: #ffffff;
  cursor: pointer;
}

.section__title,
.section__subtitle {
  font-family: "program", sans-serif;
  line-height: 0.7;
}

.section__title {
  font-size: 3.75rem;
  color: #414a4d;
  margin-top: 0.5em;
}

.section__subtitle {
  font-size: 1.69rem;
  color: #a3b9bf;
  font-style: italic;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 0.5em;
}

#cards_outer_container {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: row;
  margin: 0 auto;
  width: 100vw; */
  overflow-x: scroll;
  /* justify-content: center; */
}

#cards_inner_container {
  display: flex;
}

.tag-Container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 15px auto;
  width: 90%;
}

body {
  overflow-x: hidden;
  font-family: "Lato", sans-serif;
  color: #505962;
}
.App {
  text-align: center;
  height: 100vh;
}

#explore {
  text-decoration: none;
}

#move-up a {
  color: white;
  position: absolute;
  right: 6%;
  top: 150%;
  text-decoration: none !important;
}

